<template>
  <div class="home">
    <MglMap
      class="map"
      :accessToken="accessToken"
      :mapStyle="mapStyle"
      :center="center"
      :zoom.sync="zoom"
      @load="onMapLoaded"
    >
      <MglNavigationControl position="top-right"/>
    </MglMap>
    <div v-if="mapLoaded">
      <div v-for="(marker, name) of busyMarkers" :key="name">
        <MarkerDonut
          v-if="marker.counts.length > 0 && marker.loadPercent"
          :counts="marker.counts"
          :colors="donutColors"
          :number="marker.loadPercent"
          :totalWags="marker.totalWags"
          :freeWags="marker.freeWags"
          :lngLat="marker.lngLat"
        />
      </div>

      <v-card
        v-if="panelStation"
        class="overlay"
        width="500"
        rounded
        elevation="3"
      >
        <v-card-title>
        <span>
          {{ stationInfo.stationName }}
        </span>
          <v-spacer></v-spacer>
          <v-btn @click="panelStation = false" icon>
            <v-icon>mdi-close</v-icon>
          </v-btn>
        </v-card-title>
        <v-card-subtitle>
          {{ stationInfo.roadName }}<!--, {{station.region}} регион-->
          <v-divider/>
        </v-card-subtitle>
        <v-simple-table
          dense
        >
          <template v-slot:default>
            <thead>
            <tr>
              <th class="text-left">
                Номер<br>парка
              </th>
              <th class="text-left">
                Номер<br>пути
              </th>
              <th class="text-left">
                Наличие<br>вагонов
              </th>
              <th class="text-left">
                Занятость<br>пути
              </th>
              <th class="text-left">
                Условная<br>ёмкость
              </th>
              <th class="text-left">
                Общий отстой<br>(вагоно-сутки)
              </th>
            </tr>
            </thead>
            <tbody>
            <tr
              v-for="(way, i) in stationInfo.ways"
              :key="i"
            >
              <td v-if="way.put !== 'Всего'">
                {{ way.park }}
              </td>
              <td v-else>
                <b>{{ way.park }}</b>
              </td>
              <td v-if="way.put !== 'Всего'">
                {{ way.put }}
              </td>
              <td v-else>
              </td>
              <td v-if="way.put !== 'Всего'">
                {{ way.vag }}
              </td>
              <td v-else>
                <b>{{ way.vag }}</b>
              </td>
              <td v-if="way.put !== 'Всего'">
                {{ way.zan_dlina }}
              </td>
              <td v-else>
                <b>{{ way.zan_dlina }}</b>
              </td>
              <td v-if="way.put !== 'Всего'">
                {{ way.vid_dlina }}
              </td>
              <td v-else>
                <b>{{ way.vid_dlina }}</b>
              </td>
              <td v-if="way.put !== 'Всего'">
                {{ way.otstoi }}
              </td>
              <td v-else>
                <b>{{ way.otstoi }}</b>
              </td>
            </tr>
            </tbody>
          </template>
        </v-simple-table>
      </v-card>
      <v-card
        v-else
        class="overlay"
        width="350"
        rounded
        elevation="3"
      >
        <v-text-field
          v-model="stationSearch"
          hide-details
          prepend-inner-icon="mdi-magnify"
          solo
          label="Найти станцию"
        ></v-text-field>
        <v-list-item
          two-line
          v-for="(station, i) in filteredStations"
          :key="i"
          @click="goToStation(station.lngLat, station.esr, station.roadCode)"
        >
          <v-list-item-content>
            <v-list-item-title>{{ station.name }}</v-list-item-title>
            <v-list-item-subtitle>{{ station.railwayName }}</v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>
      </v-card>
    </div>
    <LegendRails v-if="zoom >=2 && zoom <= 4"/>
    <v-dialog
      v-model="loading"
      hide-overlay
      persistent
      width="300"
    >
      <v-card
        color="primary"
        dark
      >
        <v-card-text>
          Загрузка
          <v-progress-linear
            indeterminate
            color="white"
            class="mb-0"
          ></v-progress-linear>
        </v-card-text>
      </v-card>
    </v-dialog>
    <v-btn
      class='floatingButton'
      elevation="2"
      fab
    >
      <v-icon @click='getRail'>mdi-reload</v-icon>
    </v-btn>
  </div>
</template>

<script>
// @ is an alias to /src
import axios from 'axios'
import Mapbox from 'mapbox-gl'
import { MglMap, MglNavigationControl } from 'vue-mapbox'
import railwayFromJSON from '@/assets/uzps_dor_1.json'
import mapStyle from '@/assets/map-style.json'
import LegendRails from '@/components/LegendRails'
import MarkerDonut from '@/components/MarkerDonut'
import styleVariables from '@/css/variables.sass'

export default {
  name: 'Home',
  components: {
    LegendRails,
    MarkerDonut,
    MglMap,
    MglNavigationControl
  },

  data: () => ({
    accessToken: 'pk.eyJ1IjoiY2hpZ2EiLCJhIjoiY2tqYnRyN2NzN3B4ZTJxcWpvdmVldGdmYSJ9.mTIH5Hby4yDyU-LLFOY0Fw',
    mapStyle: mapStyle,
    zoom: 3,
    mapLoaded: false,
    center: [83.214, 60.703],
    donutColors: [
      styleVariables.donutSegment1,
      styleVariables.donutSegment2,
      styleVariables.donutSegment3,
      styleVariables.donutSegment4
    ],
    donutCounts: [1, 1, 1, 1],
    busyMarkers: [
      {
        lngLat: [],
        counts: [],
        loadPercent: null,
        totalWags: null,
        freeWags: null
      }
    ],
    popups: {},
    loading: false,
    railway: {
      dors: [{
        name: null,
        code: null,
        stations: [{
          esr: null,
          name: null,
          lngLat: [null, null],
          vid_put: null,
          vid_udv: null,
          zan_put: null,
          zan_udv: null,
          svb_put: null,
          svb_udv: null,
          zan_put_percent: null,
          zap_udv_percent: null,
          isp_put: null,
          vags: null,
          full_otstoi: null
        }]
      }]
    },
    panelStation: false,
    station: {
      name: null,
      lngLat: [],
      esr: null,
      ways: {
        public: [{
          name: null,
          length: null
        }],
        private: [{
          name: null,
          length: null
        }]
      },
      region: null,
      vid_put: null,
      vid_udv: null,
      zan_put: null,
      zan_udv: null,
      svb_put: null,
      svb_udv: null,
      zan_put_percent: null,
      zap_udv_percent: null,
      isp_put: null,
      vags: null,
      full_otstoi: null
    },
    stationInfo: {
      esr: null,
      stationName: null,
      roadName: null,
      ways: [{
        park: null,
        put: null,
        vag: null,
        zan_dlina: null,
        cv_dlina: null,
        vid_dlina: null,
        zan: null,
        otstoi: null,
        kol_pod: null,
        kol_ubor: null
      }]
    },
    stationSearch: null
  }),

  methods: {
    onMapLoaded: function (event) {
      this.$store.map = event.map
      this.mapLoaded = true

      this.createRailAreas()
      this.createStations()
      // this.updateDonutChartData()
      // this.getRail()
      this.railway = railwayFromJSON
      const map = this.$store.map
      // click and hover on station
      map.on('click', 'station-point', (event) => {
        this.openPanelStation(event.features[0].properties.esr, event.features[0].properties.roadCode) //
      })
      map.on('mouseenter', 'station-point', function () {
        map.getCanvas().style.cursor = 'pointer'
      })
      map.on('mouseleave', 'station-point', function () {
        map.getCanvas().style.cursor = ''
      })
      // popups for station
      map.on('moveend', () => {
        if (this.zoom > 9) {
          const features = map.queryRenderedFeatures({ layers: ['station-point'] })
          // let updatedPopups = []
          for (const feature of features) {
            const id = feature.id
            const coordinates = feature.geometry.coordinates
            if (!Object.prototype.hasOwnProperty.call(this.popups, id)) {
              this.popups[id] = new Mapbox.Popup({
                closeButton: false,
                closeOnClick: false
              })
                .setLngLat(coordinates)
                .setHTML(
                  `<span class="font-weight-bold">${feature.properties.name}</span>
                  <br>
                  <span style="text-decoration: underline;">Занятость путей (усл.дл.ваг.):</span>
                  <br>
                  общ.: ${feature.properties.total} / ${feature.properties.busy}
                  <br>
                  необщ.: нет данных
                  `
                )
                .addTo(map)
            } else {
              this.popups[id].setLngLat(coordinates)
            }
          }
        } else {
          for (const id in this.popups) {
            const popup = this.popups[id]
            popup.remove()
            delete this.popups[id]
          }
        }
      })
    },
    createRailAreas () {
      this.$store.map.addSource('rail-areas', {
        type: 'geojson',
        data: require('@/assets/railAreas.json')
      })
      this.$store.map.addLayer({
        id: 'rail-polygons',
        type: 'fill',
        source: 'rail-areas',
        layout: {},
        maxzoom: 6,
        paint: {
          'fill-color': [
            'match', ['get', 'code'],
            [10, 24, 61, 83, 96], styleVariables.railArea1,
            [28, 41, 63, 88], styleVariables.railArea2,
            [17, 51, 80, 92], styleVariables.railArea3,
            [1, 58, 76, 94], styleVariables.railArea4,
            /* other */ 'grey'
          ],
          'fill-opacity': [
            'interpolate', ['linear'], ['zoom'],
            0, 0.7,
            2, 0.20,
            5, 0.20,
            6, 0
          ]
        }
      })
      this.$store.map.addLayer({
        id: 'rail-borders',
        type: 'line',
        source: 'rail-areas',
        layout: {
          'line-join': 'round',
          'line-cap': 'round'
        },
        minzoom: 5.5,
        paint: {
          'line-color': styleVariables.railBorder,
          'line-width': 2,
          'line-opacity': [
            'interpolate', ['linear'], ['zoom'],
            5.5, 0,
            6.5, 0.6
          ]
        }
      })
    },
    async getRail () {
      this.loading = true
      const today = new Date()
      const paramDate = today.toISOString().slice(0, 10).replace(/-/g, '')
      try {
        const { data } = await axios.get('/uzps_dor.jsp', {
          params: {
            date: paramDate,
            ador: '00',
            avid_stan: '0',
            arod_vag: '0',
            aokpo: '0',
            aput_vid: '0'
          }
        })
        this.railway = data
        // console.log('data length ' + data.dors.length)
      } catch (e) {
        console.log('Can\'t load rail data. Error:')
        console.log(e)
      }
      this.loading = false
    },
    createStations () {
      this.$store.map.addSource('stations', {
        type: 'geojson',
        data: {
          type: 'FeatureCollection',
          features: []
        }
      })

      this.$store.map.addLayer({
        id: 'station-point',
        type: 'circle',
        source: 'stations',
        minzoom: 3.8,
        paint: {
          'circle-stroke-width': 1,
          'circle-stroke-color': 'white',
          'circle-stroke-opacity': [
            'interpolate',
            ['linear'],
            ['zoom'],
            3.8, 0,
            4, 1
          ],
          'circle-radius': {
            stops: [
              [3, 2.5],
              [6, 5],
              [12, 7]
            ]
          },
          'circle-color': [
            'interpolate',
            ['linear'],
            ['get', 'load'],
            -1, 'grey',
            0, 'green',
            0.5, 'yellow',
            1, 'red'
          ],
          'circle-opacity': [
            'interpolate',
            ['linear'],
            ['zoom'],
            3.8, 0,
            4, 1
          ]
        }
      })
    },
    getRoadsGeoJSON () {
      const stationsFeaturesAllRoads = this.railway.dors.reduce((accumulator, road) => {
        if (road.stations.length > 0) {
          const stationsGeoJSON = this.getStationsGeoJSON(road)
          accumulator = accumulator.concat(stationsGeoJSON.features)
        }
        return accumulator
      }, [])

      return {
        type: 'FeatureCollection',
        features: stationsFeaturesAllRoads
      }
    },
    getStationsGeoJSON (road) {
      const stationsFeatures = road.stations.map(station => {
        const total = station.vid_udv ? station.vid_udv : 0
        const busy = station.zan_udv ? station.zan_udv : 0
        const free = station.svb_udv ? station.svb_udv : 0
        const load = station.zap_udv_percent / 100
        station.load = load
        return {
          type: 'Feature',
          id: station.esr,
          properties: {
            load,
            name: station.name,
            esr: station.esr,
            roadCode: road.code,
            total,
            busy,
            free
          },
          geometry: {
            type: 'Point',
            coordinates: station.lngLat
          }
        }
      })
      return {
        type: 'FeatureCollection',
        features: stationsFeatures
      }
    },
    updateDonutChartData () {
      const coordinatesMap = {
        '01': [31.570782, 60.868906],
        17: [36.724702, 53.889617],
        92: [108.191104, 53.626679],
        51: [42.598601, 45.707442],
        96: [132.20322, 49.712039],
        94: [119.10244, 53.795598],
        61: [46.002965, 49.115728],
        24: [49.439791, 57.691804],
        83: [80.111098, 55.050281],
        10: [21.262665, 54.806304],
        88: [93.449533, 55.668329],
        63: [49.74429, 54.232178],
        76: [68.875671, 62.062656],
        28: [54.752627, 64.463401],
        58: [40.218175, 51.563952],
        80: [59.424326, 53.67003],
        85: [34.559016, 45.295494]
      }
      // console.log('this.railway.dors --- ' + this.railway.dors.length)
      this.busyMarkers = []
      for (const road of this.railway.dors) {
        const { total, busy } = road.stations.reduce((accumulator, station) => {
          const total = station.vid_udv ? station.vid_udv : 0
          const busy = station.zan_udv > 0 ? station.zan_udv : 0
          // const free = total ? station.zan_udv > 0 ? station.zan_udv : 0 : 0
          return {
            total: accumulator.total + total,
            busy: accumulator.busy + busy
          }
        }, { total: 0, busy: 0 })

        let loadPercent = 0
        if (total > 0) {
          loadPercent = Math.round(busy / total * 100)
        }
        // console.log('Дорога: ' + road.name + ' К.: ' + coordinatesMap[road.code])
        this.busyMarkers.push({
          lngLat: coordinatesMap[road.code],
          counts: [busy, total - busy, 0, 0],
          loadPercent: loadPercent,
          totalWags: total,
          freeWags: total - busy
        })
      }
    },
    async openPanelStation (esr, roadCode) {
      const today = new Date()
      const paramDate = today.toISOString().slice(0, 10).replace(/-/g, '')
      try {
        const { data } = await axios.get('/uzps_stan.jsp', {
          params: {
            date: paramDate,
            ador: roadCode,
            astan: esr,
            avid_stan: '0',
            arod_vag: '0',
            aokpo: '0',
            aput_vid: '0'
          }
        })
        this.stationInfo = data
        console.log(this.stationInfo.stationName)
      } catch (e) {
        console.log('Can\'t load rail data. Error:')
        console.log(e)
      }
    },
    goToStation (lngLat, esr, roadCode) {
      this.$store.map.flyTo({
        center: lngLat,
        zoom: 10,
        bearing: 0,
        speed: 2,
        essential: true
      })
      this.stationSearch = ''
      this.openPanelStation(esr, roadCode)
    }
  },

  computed: {
    filteredStations () {
      if (!this.stationSearch) return []
      const a = this.railway.dors
        // add railway name to station
        .map(railway => {
          const railwayName = railway.name
          const roadCode = railway.code
          railway.stations = railway.stations.map(station => {
            return {
              ...station,
              railwayName,
              roadCode
            }
          })
          return railway
        })
        // take all stations
        .reduce((accumulator, railway) => accumulator.concat(railway.stations), [])
        // filter by user input
        .filter(station => station.name.toLowerCase().indexOf(this.stationSearch.toLowerCase()) === 0)
        // sort by alphabet
        .sort((a, b) => a.name.toLowerCase() > b.name.toLowerCase() ? 1 : -1)
        // slice first 10 elements
        .slice(0, 10)
      console.log(a)
      return a
    }
  },

  watch: {
    railway: {
      handler: function () {
        const roadsGeoJSON = this.getRoadsGeoJSON()
        // console.log(roadsGeoJSON)
        this.$store.map.getSource('stations').setData(roadsGeoJSON)
        this.updateDonutChartData()
      },
      deep: true
    }
  }
}
</script>

<style scoped lang="sass">
.map
  position: absolute
  top: 0
  bottom: 0
  width: 100%

.overlay
  position: absolute
  user-select: none
  margin: 10px

.floatingButton
  box-shadow: 0 0 0 2px rgba(0,0,0,.1) !important
  position: absolute
  right: 0
  top: 0
  margin: 110px 10px
  border-radius: 4px !important
  width: 29px !important
  height: 29px !important
</style>
