<template>
  <MarkerDefault :lngLat="lngLat">
    <div>
      <table>
        <tr>
          <td align='center'>
            <svg
              :width="w"
              :height="w"
              :viewBox="`0 0 ${w} ${w}`"
              text-anchor="middle"
              :style="`font: ${fontSize}px sans-serif; display: block`"
            >
              <path
                v-for="(segment, i) of segments"
                :key="i"
                :d="segment.d" :fill="segment.color" />
              <circle
                :cx="r"
                :cy="r"
                :r="r0"
                fill="white"
              />
              <path :d="border.d" :fill="border.color" />
              <text dominant-baseline="central" :transform="`translate(${r}, ${r})`">
                {{ number }}%
              </text>
            </svg>
          </td>
        </tr>
        <tr>
          <td>
            <v-sheet
            color="white"
            elevation="1"
            height="60"
            width="120"
            style="padding: 2px"
            >
            <b>Занятость:</b><br>
            Общ: {{totalWags}} / {{freeWags}}
            <br>
            Необщ: нет данных
            </v-sheet>
          </td>
        </tr>
      </table>
    </div>
  </MarkerDefault>
</template>

<script>
import MarkerDefault from '@/components/MarkerDefault'

export default {
  name: 'MarkerDonut',
  components: { MarkerDefault },
  data: () => ({
    w: undefined,
    fontSize: undefined,
    r: undefined,
    r0: undefined,
    total: undefined,
    segments: [],
    border: { d: null, color: null }
  }),
  props: {
    lngLat: {
      type: Array,
      required: true
    },
    counts: {
      type: Array,
      required: true
    },
    colors: {
      type: Array,
      required: true
    },
    number: {
      type: Number
    },
    totalWags: {
      type: Number
    },
    freeWags: {
      type: Number
    }
  },
  created () {
    this.donutCreate()
  },
  methods: {
    donutCreate () {
      this.offsets = []
      this.total = 0
      for (let i = 0; i < this.counts.length; i++) {
        this.offsets.push(this.total)
        this.total += this.counts[i]
      }
      this.fontSize = this.number >= 1000 ? 22 : this.number >= 100 ? 20 : 18
      this.r = this.number >= 1000 ? 50 : this.number >= 100 ? 32 : 30
      this.r0 = Math.round(this.r * 0.6)
      this.w = this.r * 2

      this.segments = []
      for (let i = 0; i < this.counts.length; i++) {
        this.segments.push(this.donutSegment(
          this.offsets[i] / this.total,
          (this.offsets[i] + this.counts[i]) / this.total,
          this.r,
          this.r0,
          this.colors[i]
        ))
      }
      this.border = this.donutSegment(0, 1, this.r, this.r - 2, 'white')
    },
    donutSegment (start, end, r, r0, color) {
      if (end - start === 1) end -= 0.00001
      const a0 = 2 * Math.PI * (start - 0.25)
      const a1 = 2 * Math.PI * (end - 0.25)
      const x0 = Math.cos(a0)
      const y0 = Math.sin(a0)
      const x1 = Math.cos(a1)
      const y1 = Math.sin(a1)
      const largeArc = end - start > 0.5 ? 1 : 0
      const d = [
        'M',
        r + r0 * x0,
        r + r0 * y0,
        'L',
        r + r * x0,
        r + r * y0,
        'A',
        r,
        r,
        0,
        largeArc,
        1,
        r + r * x1,
        r + r * y1,
        'L',
        r + r0 * x1,
        r + r0 * y1,
        'A',
        r0,
        r0,
        0,
        largeArc,
        0,
        r + r0 * x0,
        r + r0 * y0
      ].join(' ')
      return {
        d,
        color
      }
    }
  },
  watch: {
    colors: function () {
      this.donutCreate()
    },
    counts: function () {
      this.donutCreate()
    },
    number: function () {
      this.donutCreate()
    },
    totalWags: function () {
      this.donutCreate()
    },
    freeWags: function () {
      this.donutCreate()
    }
  }
}
</script>
